
.invisible{
  display: none   !important;
}

.card {

 display: flex;
 width: 100%;
 border: 1px solid black;
 border-radius: 5px;
 margin-bottom: 10px;
}
.card-input{
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0 1px 0 rgb(9 30 66 / 25%);
  display: block;
  width: 91%;
  min-height: 30px;
  margin-bottom: 12px;
  border: none;
  padding: 5px;
}
.card-input::placeholder{
  font-family: 'Times New Roman';
  font-size: 15px;
 }
.btns{
  display: flex;
}
.card-btn-add{
  background-color: #6b778c;
  box-shadow: none;
  border: none;
  color: #000;
  font-family: 'Times New Roman';
  font-size: 17px;
  font-weight: 400;
  box-sizing: border-box;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  cursor: pointer;
  padding: 6px 8px;
  margin-right: 6px;
  margin-bottom: 9px;
}
.card-btn-add:hover{
  background-color: #4c6186;
}
.card-btn-close{
  display: inline-flex;
  box-shadow: none;
  border: none;
  font-family: 'Times New Roman';
  font-size: 17px;
  cursor: pointer;
}
.card__left-part {
  min-height: 25px;
  margin-left: 5px;
  font-size: 16px;
  padding: 5px;
  min-width: 80%;
  
}

.card-title{
  
  background-color: #fff;
  border-radius: 3px;
  box-shadow: 0 1px 0 rgb(9 30 66 / 25%);
  display: flex;
  align-items: center;
  min-height: 25px;
  font-size: 16px;
  padding: 5px;
  margin-bottom: 5px;
  overflow:scroll;
}
.new-card__description {
  background-color: #fff;
  border-radius: 3px;
  box-shadow: 0 1px 0 rgb(9 30 66 / 25%);
  display: flex;
  align-items: center;
  min-height: 25px;
  font-size: 16px;
  margin: 5px 0;
  padding: 5px;
  cursor: pointer;
}
.card-title{
  word-wrap: break-word;
}
.card-title:hover{
  cursor: pointer;
}
.card-ico {
  padding: 4px;
  display: flex;
}
.card-ico:hover, .delite-card:hover {
  border-radius: 5px;
  background-color: rgb(145, 143, 138);

  cursor: pointer;
}
.delite-card {
  font-size: 30px;
  font-weight: 900;
  padding: 0 7px;
}

.invalid{
  border: 1.4px solid rgb(175, 77, 77);;
}
.invalid::placeholder{
  color: rgb(192, 52, 52);
}
