@font-face {
  font-family: 'ico-footer';
  src:
    url('/src/components/modal-window/ico--x/ico-footer.ttf') format('truetype'),
    url('/src/components/modal-window/ico--x/ico-footer.woff') format('woff'),
    url('/src/components/modal-window/ico--x/ico-footer.svg') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="ic-"], [class*=" ic-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'ico-footer' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.ic-close:before {
  content: "\e901";
}
